function printTodos() {
  todoList.innerHTML = null;
  todos.forEach((todo) => {
    let todoName = todo.todoText;
    console.log(todo.todoText);
    let newTodo = document.createElement("li");
    newTodo.textContent = todoName;
    newTodo.addEventListener("click", () => {
      if (todo.todoComplete === true) {
        todo.todoComplete = false;
        printTodos();
        // displayComplete();
      } else {
        todo.todoComplete = true;
        printTodos();
        // displayComplete();
      }
    });
    todoList.appendChild(newTodo);
    if (todo.todoComplete === true) {
      newTodo.className = "done";
      console.log(todo);
    } else {
      newTodo.className = " ";
      console.log(todo);
    }
  });
}

